class Mask {
  constructor(selector, regExpString = '+7 (___) ___-__-__') {
    this.elements = document.querySelectorAll(selector);
    this.regExpString = regExpString;
  }

  init() {
    this._listeners(this.elements);
  }

  _listeners(selector) {
    for (let i = 0; i < selector.length; i++) {
      const input = selector[i];
      input.addEventListener('input', this._mask.bind(this), false);
      input.addEventListener('focus', this._mask.bind(this), false);
      input.addEventListener('blur', this._mask.bind(this), false);
    }
  }

  _setCursorPosition(pos, elem) {
    elem.focus();
    if (elem.setSelectionRange) {
      elem.setSelectionRange(pos, pos);
    } else if (elem.createTextRange) {
      const range = elem.createTextRange();
      range.collapse(true);
      range.moveEnd('character', pos);
      range.moveStart('character', pos);
      range.select();
    }
  }

  _mask(event) {
    let matrix = this.regExpString,
      i = 0,
      def = matrix.replace(/\D/g, ''),
      val = event.target.value.replace(/\D/g, '');
    if (def.length >= val.length) {
      val = def;
    }
    event.target.value = matrix.replace(/./g, function(a) {
      return /[_\d]/.test(a) && i < val.length ? val.charAt(i++) : i >= val.length ? '' : a
    });
    if (event.type == 'blur') {
      if (event.target.value.length == 2) {
        event.target.value = '';
      }
    } else {
      this._setCursorPosition(event.target.value.length, event.target);
    }
  }
}

const utils = {
  cache: {},
  // сет брейкпоинтов для js
  // должны совпадать с теми что в body:after
  mediaBreakpoint: {
    sm: 576,
    md: 768,
    lg: 992,
    xl: 1366,
    xxl: 1920,
  },
  isMobile: {
    Android() {
      return navigator.userAgent.match(/Android/i);
    },
    BlackBerry() {
      return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS() {
      return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera() {
      return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows() {
      return navigator.userAgent.match(/IEMobile/i);
    },
    any() {
      return (this.Android() || this.BlackBerry() || this.iOS() || this.Opera() || this.Windows());
    },
  },
  svgIcons() {
    const container = document.querySelector('[data-svg-path]');
    const path = container.getAttribute('data-svg-path');
    const xhr = new XMLHttpRequest();
    xhr.onload = () => {
      container.innerHTML = xhr.responseText;
    };
    xhr.open('get', path, true);
    xhr.send();
  },
  detectIE() {
    /**
     * detect IE
     * returns version of IE or false, if browser is not Internet Explorer
     */

     (function detectIE() {
       var ua = window.navigator.userAgent;

       var msie = ua.indexOf('MSIE ');
       if (msie > 0) {
        // IE 10 or older => return version number
        var ieV = parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
        document.querySelector('body').className += ' IE';
      }

      var trident = ua.indexOf('Trident/');
      if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        var ieV = parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
        document.querySelector('body').className += ' IE';
      }

      var edge = ua.indexOf('Edge/');
      if (edge > 0) {
        // IE 12 (aka Edge) => return version number
        var ieV = parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
        document.querySelector('body').className += ' IE';
      }

      // other browser
      return false;
    })();
  },
  truncateText(elements) {
    const cutText = () => {
      for (let i = 0; i < elements.length; i++) {
        const text = elements[i];
        const elemMaxHeight = parseInt(getComputedStyle(text).maxHeight, 10);
        const elemHeight = text.offsetHeight;
        const maxHeight = elemMaxHeight ? elemMaxHeight : elemHeight;

        shave(text, maxHeight);
      }
    };

    this.cache.cutTextListener = this.throttle(cutText, 100);

    cutText();

    window.addEventListener('resize', this.cache.cutTextListener);
  },
  throttle(callback, limit) {
    let wait = false;

    return () => {
      if (!wait) {
        callback.call();
        wait = true;
        setTimeout(() => {
          wait = false;
        }, limit);
      }
    };
  },
  getScreenSize() {
    let screenSize = window.getComputedStyle(document.querySelector('body'), ':after').getPropertyValue('content');
    screenSize = parseInt(screenSize.match(/\d+/), 10);
    return screenSize;
  },
  polyfills() {
    /**
     * polyfill for elem.closest
     */
    (function(ELEMENT) {
      ELEMENT.matches = ELEMENT.matches || ELEMENT.mozMatchesSelector || ELEMENT.msMatchesSelector || ELEMENT.oMatchesSelector || ELEMENT.webkitMatchesSelector;
      ELEMENT.closest = ELEMENT.closest || function closest(selector) {
        if (!this) return null;
        if (this.matches(selector)) return this;
        if (!this.parentElement) {
          return null;
        } else {
          return this.parentElement.closest(selector);
        } 
      };
    }(Element.prototype));

    /**
     * polyfill for elem.hasClass
     */
    Element.prototype.hasClass = function(className) {
      return this.className && new RegExp("(^|\\s)" + className + "(\\s|$)").test(this.className);
    };
  },
};

const APP = {
  animation_time: '.3s',
  speed_sliders: 800,
  arrayMobile: [[document.querySelectorAll('.main__slider.swiper-container'), '77.33333333333333', '.main__slider.swiper-container .swiper-slide']],
  arrayTablet: [[document.querySelectorAll('.main__slider.swiper-container'), '55.33854166666667', '.main__slider.swiper-container .swiper-slide']],
  arrayDesktope: [[document.querySelectorAll('.main__slider.swiper-container'), '93.09090909090909', '.main__slider.swiper-container .swiper-slide']],
  mediaMobile: window.matchMedia('(max-width: 767px)'),
  mediaTablet: window.matchMedia('(max-width: 1366px)'),
  mediaDesktope: window.matchMedia('(max-width: 10000px)'),
  mediaMinDesktope: window.matchMedia('(max-width: 1366px)'),
  mediaMinDesktope: window.matchMedia('(min-width: 1366px)'),
  mediaMinTablet: window.matchMedia('(min-width: 768px)'),
  mediaMinMobile: window.matchMedia('(min-width: 1px)'),
  minDesktope: 1366,
  timeout: 300,
  photoSlider: '',

  initBefore() {
    utils.polyfills();
    utils.svgIcons();
    document.documentElement.className = document.documentElement.className.replace('no-js', 'js');
  },

  init() {
    utils.detectIE();
    //APP.lazyload();

    const myMask = new Mask('.js-tel');
    myMask.init();

    //APP.buttons();
    //APP.closeOnFocusLost();
    APP.accordoin();
  },

  initOnLoad() {
    utils.truncateText(document.querySelectorAll('.js-dot'));
  },

  accordoin()
  {
    const timeout = 300;
    window.addEventListener('DOMContentLoaded', () =>
    {
      const accordions = document.querySelectorAll('.js-paragraph');
      const accordions_block = document.querySelectorAll('.js-accordoinlist');
      const accordions_list = document.querySelectorAll('.js-list');
      accordions[0].classList.add('active');
      accordions_block[0].style.height = `${accordions_list[0].offsetHeight}px`;
      setTimeout(() => { accordions_block[0].style.height = "auto"; }, timeout);
      for(let i = 0; i < accordions.length; i++)
      {
        accordions[i].addEventListener('click', () =>
        {
          if (!accordions[i].classList.contains('active'))
          {
            for (let a = 0; a < accordions.length; a++)
            {
              if (accordions[a].classList.contains('active'))
              {
                accordions[a].classList.remove('active');
                accordions_block[a].style.height = `${accordions_list[a].offsetHeight}px`;
                setTimeout(() =>
                {
                  accordions_block[a].style.height = "0px";
                }, 10);
              }
            }
            accordions[i].classList.add('active');
            accordions_block[i].style.height = `${accordions_list[i].offsetHeight}px`;
            setTimeout(() => { accordions_block[i].style.height = "auto"; }, timeout);
          }
        });
      }
    });
  },

  resolution(arr)
  {
    for (let i = 0; i < arr.length; i++)
    {
      if (arr[i][0][0])
      {
        const blocks = document.querySelectorAll(arr[i][2]);
        const minHeight = `${Number(window.getComputedStyle(arr[i][0][0], null).width.replace(/px/, '')) / 100 * Number(arr[i][1])}px`;
        for (let a = 0; a < blocks.length; a++)
        {
          if (blocks[a].style.minHeight !== minHeight &&
            window.innerWidth < 1366)
          {
            blocks[a].style.minHeight = minHeight;
            //console.log(blocks[a].style.minHeight);
          }
        }
      }
    }
  }
};

APP.initBefore();

document.addEventListener('DOMContentLoaded', () => {
  APP.init();
  if (APP.mediaMobile.matches)
  {
    APP.resolution(APP.arrayMobile);
  }
  else if (APP.mediaTablet.matches)
  {
    APP.resolution(APP.arrayTablet);
  }
  else if (APP.mediaDesktope.matches)
  {
    APP.resolution(APP.arrayDesktope);
  }
});

window.addEventListener('resize', () =>
{
  if (APP.mediaMobile.matches)
  {
    APP.resolution(APP.arrayMobile);
  }
  else if (APP.mediaTablet.matches)
  {
    APP.resolution(APP.arrayTablet);
  }
  else if (APP.mediaDesktope.matches)
  {
    APP.resolution(APP.arrayDesktope);
  }
});



window.onload = () => {
  APP.initOnLoad();
};
